export class Navigation {
    constructor() {
        this.element = null;
        this.init();
    }
    
    init() {
        this.element = document.createElement('nav');
        this.element.className = 'main-nav';
        this.element.innerHTML = `
            <div class="nav-container">
                <a href="/" class="nav-logo">
                    <span class="logo-text">IsperAI</span>
                </a>
                
                <div class="nav-links">
                    <a href="/isper" class="nav-link">Maps</a>
                    <a href="/company" class="nav-link">Isper</a>
                    <!-- <a href="/news" class="nav-link">News</a> Disabled -->
                    <a href="/contact" class="nav-link">Contact</a>
                </div>
                
                <div class="nav-mobile-toggle">
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </div>
            <style>
                .main-nav {
                    border-bottom-color: transparent !important;
                    transition: border-color 0.3s ease;
                }
                
                .logo-text {
                    font-family: 'Poppins', sans-serif;
                    font-weight: 600;
                    font-size: 1.5rem;
                    color: white;
                    text-decoration: none;
                    letter-spacing: -0.02em;
                }
                
                .nav-logo {
                    text-decoration: none;
                }
                
                .nav-logo:hover .logo-text {
                    color: #E0E0E0;
                    transition: color 0.2s ease;
                }
                
                .main-nav.scrolled {
                    border-bottom-color: var(--border-gray) !important;
                }
                
                @media (max-width: 768px) {
                    .nav-links:not(.active) {
                        border-bottom: none !important;
                    }
                }
            </style>
        `;
        
        this.setupMobileToggle();
        this.updateActiveLink();
        this.setupLinkHandlers();
        this.setupScrollBehavior();
    }
    
    setupMobileToggle() {
        const toggle = this.element.querySelector('.nav-mobile-toggle');
        const links = this.element.querySelector('.nav-links');
        const navLinks = this.element.querySelectorAll('.nav-link');
        
        toggle.addEventListener('click', () => {
            links.classList.toggle('active');
            toggle.classList.toggle('active');
        });
        
        navLinks.forEach(link => {
            link.addEventListener('click', () => {
                links.classList.remove('active');
                toggle.classList.remove('active');
            });
        });
    }
    
    setupLinkHandlers() {
        const links = this.element.querySelectorAll('.nav-link');
        const logo = this.element.querySelector('.nav-logo');
        
        links.forEach(link => {
            link.addEventListener('click', (e) => {
                const href = link.getAttribute('href');
                const currentPath = window.location.pathname;
                
                if (href === currentPath) {
                    e.preventDefault();
                }
            });
        });
        
        logo.addEventListener('click', (e) => {
            const currentPath = window.location.pathname;
            
            if (currentPath === '/' || currentPath === '') {
                e.preventDefault();
            }
        });
    }
    
    updateActiveLink() {
        const currentPath = window.location.pathname;
        const links = this.element.querySelectorAll('.nav-link');
        
        links.forEach(link => {
            if (link.getAttribute('href') === currentPath) {
                link.classList.add('active');
            } else {
                link.classList.remove('active');
            }
        });
    }
    
    setupScrollBehavior() {
        const handleScroll = () => {
            if (window.scrollY > 10) {
                this.element.classList.add('scrolled');
            } else {
                this.element.classList.remove('scrolled');
            }
        };
        
        handleScroll();
        
        window.addEventListener('scroll', handleScroll);
    }
    
    render() {
        return this.element;
    }
}